.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
}

.dialogsItems {
    padding: 20px;    
    border-right: 1px solid white;
}


.dialogsItems a {
    color: white;
}


.dialogsItems .active{
    color: red;
}








.messages {
    color: white;
    padding: 20px;

}

